.Toolbar {
  height: 56px;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #703b09;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  box-sizing: border-box;
  z-index: 90;

  nav {
    height: 100%;
  }
}
.Toolbar-logo {
  height: 80%;
}
@media (max-width: 499px) {
  .DesktopOnly {
    display: none;
  }
}