.NavigationItem {
  margin: 10px 0;
  display: block;
  box-sizing: border-box;
  width: 100%;

  a {
    display: block; 
    color: #8f5c2c;
    text-decoration: none;
    width: 100%;
    
    box-sizing: border-box;

  }
}
.NavigationItem a.active,
.NavigationItem a:hover, 
.NavigationItem a:active {
  color: #40a4c8;
}

@media (min-width: 500px) {
  .NavigationItem {
    margin: 0;
    box-sizing: border-box;
    display: flex;
    width: auto;
    height: 100%;
    align-items: center;
  
    a {
      color: #fff;
      height: 100%;
      padding: 16px 10px;
      border-bottom: 4px solid transparent;
      
  
    }
  }
  .NavigationItem a.active,
  .NavigationItem a:hover, 
  .NavigationItem a:active {
    background-color: #8f5c2c;
    border-bottom: 4px solid #40a4c8;
    color: #fff;
  }
}


